body {
  background-color: rgb(234, 237, 237);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.home  {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}

.home__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}
 .home__filter {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    z-index: 100;
    margin-left: 5px;
    margin-right: 5px;
    position: -webkit-sticky;
    position: sticky;
    top: 65px;
 }
.home__image {
    width: 100%;
    z-index: -1;
    margin-bottom: -150px;
    -webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
            mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
    height: 500px;
}


.home__product {
    display: flex;
    flex-direction: column;
    background-color: white;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 250px;
    max-height: 430px;
    z-index: 1;
    background-color: white;
    /* background-color: #f1f1f1; */
    /* max-width: 25%; */
    /* font-size: 30px; */
}
.home__product:hover {
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}
.home__product__rating {
    display: flex ;
    width: 200px;
}
.home__product__image {
    height: 200px;
    width: 100%;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    margin-bottom: 15px;
}

.home__product button {
    background: #F0C14B;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}

.home__product__price {
    margin-top: 5px;
}

.home__product__info {
    height: 200px;
    margin-bottom: 15px; 
}
.filter__container {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #131921;;
     z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
    color: white;
    padding: 3px 20px;
    border-radius: 50px;
}
.vertical__line {
    border-left: 2px solid #cd9042;;
    height:25px;
    margin: 0px 3px;
}
.filter__container > h3 {
    color: #cd9042;
}
.filter__container > input {
    margin: 0px 3px;
    color: #cd9042;
}
.filter__container  > select {
    margin: 0px 3px;
    color: #cd9042;
    border-radius: 50px;
    padding: 10px 10px;
}
.filter__container > label {
    padding: 0px 10px;
}
.checkout {
    display: flex;
    padding: 20px;
    /* background-color: white; */
    /* height: max-content; */
}
.checkout__product{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: 5px;
    margin-right: 5px;
    /* margin-bottom: 5px; */
}
.checkout__ad {
    width: 100%;
    margin-bottom: 10px;
}
.checkout__title {
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray;
}
.subtotal {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    height: 100px;
    height: 100px;
    padding: 20px;
    background-color: #f3f3f3;
    border: 1px solid #dddddd;
    border-radius: 3px;
}
.subtotal__gift {
    display: flex;
    align-items: center;
}

.subtotal__gift > input {
    margin-right: 5px;
}

.subtotal button {
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}
.product {
    display: flex;
    flex-direction: row;
    background-color: white;
    justify-content: center;
    margin: 1em;
    /* padding: 2em; */
    /* width: 100%; */
    /* height: 250px; */
    /* min-width: 10em; */
    z-index: 1;
}
.product:hover {
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}
.product__rating {
    display: flex ;
}

.product__image {
    max-height: 300px;
    width: 30em !important;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    margin: 20px;
}
.product button {
    background: #F0C14B;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}

.product__price {
    /* margin-top: 5px; */
}

.product__info {
    min-height: 300px;
    padding: 30px;
}
.login {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.login__logo {
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
}
.login__container {
    width: 300px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgrey;
    padding: 20px;
}
.login__container > h1 {
    font-weight: 500;
    margin-bottom: 20px;
}
.login__container > form  > h5 {
    margin-bottom: 5px;
}
.login__container > form > input {
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}
.login__container > p{
    margin-top: 15px;
    font-size: 12px;
}
.login__signInButton {
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}
.login__registerButton {
    border-radius: 2px;
    width: 100%;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgray;
}
.header {
  height: 60px;
  display: flex;
  align-items: center;
  background-color: #131921;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
}
.sign__out__user {
  cursor: pointer;
}
.header__logo {
  width: 100px;
  object-fit: contain;
  margin: 0 20px;
  margin-top: 18px;
}
.header__search {
  display: flex;
  flex: 1 1;
  align-items: center;
  border-radius: 24px;
}
.header__searchInput {
  height: 9.5px;
  padding: 10px;
  boder:  none;
  width: 100%;
}
.header__searchIcon {
  padding: 5px;
  height:  22px !important;
  background-color: #cd9042;
}
.header__optionLineOne {
  font-size: 10px;
}
.header__optionLineTwo {
  font-size: 13px;
  font-weight: 800;
}
.header__nav {
  display: flex;
  justify-content: space-evenly;
}
.header__optionBasket{
  display: flex;
  align-items: center;
  color: white;
}
.header__option {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  color: white;
}
.basket__link{
  color: white;
  padding-right: 10px;
  text-decoration: none;
}
