.home__product {
    display: flex;
    flex-direction: column;
    background-color: white;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 250px;
    max-height: 430px;
    z-index: 1;
    background-color: white;
    /* background-color: #f1f1f1; */
    /* max-width: 25%; */
    /* font-size: 30px; */
}
.home__product:hover {
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}
.home__product__rating {
    display: flex ;
    width: 200px;
}
.home__product__image {
    height: 200px;
    width: 100%;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    margin-bottom: 15px;
}

.home__product button {
    background: #F0C14B;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}

.home__product__price {
    margin-top: 5px;
}

.home__product__info {
    height: 200px;
    margin-bottom: 15px; 
}